/* ----------------------- 

	Typografica 

----------------------- */

/* --- main --- */

h1, h2, h3, h4, h5, h6, ul {
	padding: 0;
	margin: 0;
}

h1, .h1 {	
	font-family: $font-stack;
	font-size: 48px;
  	font-weight: bold;
  	font-style: normal;
  	font-stretch: normal;
  	line-height: 1.29;
  	letter-spacing: normal;
	color: $grayBlack;

	margin-bottom: 30px;
}

@media (max-width: 768px) {
	h1, .h1 {	
	  	font-family: $font-stack;
	  	font-size: 22px;
	  	font-weight: bold;
	  	font-style: normal;
	  	font-stretch: normal;
	  	line-height: normal;
	  	letter-spacing: normal;
	}
}

h2, .h2 {
	font-family: $font-stack;
	font-size: 40px;
  	font-weight: bold;
  	font-style: normal;
  	font-stretch: normal;
  	line-height: 1.2;
  	letter-spacing: normal;
	color: $grayBlack;
}

h3, .h3 {
	font-family: $font-stack;
	font-size: 33px;
  	font-weight: bold;
  	font-style: normal;
  	font-stretch: normal;
  	line-height: 1.7;
  	letter-spacing: normal;
	color: $grayBlack;
}

h4, .h4 {
	font-family: $font-stack;
	font-size: 27px;
  	font-weight: bold;
  	font-style: normal;
  	font-stretch: normal;
  	line-height: 1.7;
  	letter-spacing: normal;
	color: $grayBlack;
}

h5, .h5 {
	font-family: $font-stack;
	font-size: 23px;
  	font-weight: bold;
  	font-style: normal;
  	font-stretch: normal;
  	line-height: 1.7;
  	letter-spacing: normal;
	color: $grayBlack;
}

h6, .h6 {
	font-family: $font-stack;
	font-size: 17px;
  	font-weight: bold;
  	font-style: normal;
  	font-stretch: normal;
  	line-height: 1.29;
  	letter-spacing: normal;
	color: $grayBlack;
}

.subtitle {
	color: $light-gray;
	font-family: $font-stack;
	font-style: normal;
	font-weight: normal;
	font-size: 12px;
	line-height: 14px;/* identical to box height */
}

.button-1 {
	font-family: $font-stack;
  	font-size: 17px;
  	font-weight: bold;
  	font-style: normal;
  	font-stretch: normal;
  	line-height: 1.29;
  	letter-spacing: normal;
