/* -----------------------

	Main style

----------------------- */

body {
	font-family: $font-stack;
	font-size: 16px;
	width: 100%;
	height: 100%;
	/*position: fixed;
	display: inline-flex;
	flex-direction: row-reverse;
	align-items: center;
	justify-content: center;*/
}

img {
	width: 100%;
}

.img-responsive {
	max-width: 500px;
	width: 100%;
	height: auto;
}

section {
	margin: 20px 0px;
}

@media (max-width: 480px) {
	section {
	    margin: 0px 0px;
	}
}

time {
	font-family: $font-stack;
 	font-size: 12px;
  	font-weight: normal;
  	font-style: normal;
  	font-stretch: normal;
  	line-height: normal;
  	letter-spacing: normal;
  	color: $light-gray;
}

hr {
	color: $light-gray;
    background-color: $light-gray;
    border: 0px none;
    height: 2px;
    clear: both;
    margin: 60px 0px;
}

@media (max-width: 991px) {
	hr {
	    margin: 40px 0px;
	}
}

.hr {
	color: $light-gray;
    background-color: $light-gray;
    border: 0px none;
    height: 1px;
    margin: 30px 0px;
    clear: both;
}
/* Other elements */

.applicationButton {
	display: inline-block;
	width: 100%;
	border-radius: 22px;
	border: none;
	background-color: $bleu;
	padding: 12px 0px;
	color: $white;
	font-size: 12px;
	font-weight: bold;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.33;
	letter-spacing: normal;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	transition: all .3s ease-out;
	&:hover {
		transition: all .3s ease-out;
		background-color: $light-gray; 
		color: $gray;
	}
}

.applicationButton--narrow {
	padding: 12px 95px;
}

.applicationButton--disabled {
	display: inline-block;
	width: 100%;
	border-radius: 22px;
	border: none;
	background-color: $bleu;
	padding: 12px 0px;
	color: $white;
	font-size: 12px;
	font-weight: bold;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.33;
	letter-spacing: normal;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	transition: all .3s ease-out;
	opacity: 0.3;
}

.applicationButton--hover {
	display: inline-block;
	width: 100%;
	border-radius: 22px;
	border: none;
	background-color: $light-gray; 
	padding: 12px 0px;
	color: $gray;
	font-size: 12px;
	font-weight: bold;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.33;
	letter-spacing: normal;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	transition: all .3s ease-out;
}

.applicationButtonLine {
	display: inline-block;
	width: 100%;
	border-radius: 22px;
	border: 2px solid $bleu;
	background-color: rgba(82, 87, 242, 0.1);
	padding: 12px 0px;
	color: $bleu;
	font-size: 12px;
	font-weight: bold;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.33;
	letter-spacing: normal;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	transition: all .3s ease-out;
	&:hover {
		background-color: rgba(255, 255, 255, 1); 
	}
}

.applicationButtonLine--hover {
	display: inline-block;
	width: 100%;
	border-radius: 22px;
	border: 2px solid $bleu;
	background-color: rgba(82, 87, 242, 0.1);
	padding: 12px 0px;
	color: $bleu;
	font-size: 12px;
	font-weight: bold;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.33;
	letter-spacing: normal;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	transition: all .3s ease-out;
}

.applicationButtonLine--disabled {
	display: inline-block;
	width: 100%;
	border-radius: 22px;
	border: 2px solid $bleu;
	background-color: none;
	padding: 12px 0px;
	color: $bleu;
	font-size: 12px;
	font-weight: bold;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.33;
	letter-spacing: normal;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	transition: all .3s ease-out;
	opacity: 0.3;
}


